@import url(https://fonts.googleapis.com/css?family=Lato&display=swap);
@import url(https://fonts.googleapis.com/css?family=Raleway&display=swap);
body{
    margin:0;
}

.navBar{
    height:60px;
}

.link{
    font-family: 'Raleway', Arial, sans-serif;
    text-decoration: none;
    color: rgb(184, 183, 173);
    padding: 5px 0;
}

.link:hover{
    text-decoration: underline;
    color: rgb(75, 198, 185);
}



/* Position and sizing of burger button */
.bm-burger-button{
    position: absolute;
    width: 36px;
    height: 30px;
    right: 18px;
    top: 18px;
}

.logo{
    margin:10px;
    height:45px;
    left:18px;
}

/* Color/shape of burger icon bars */
.bm-burger-bars {
    background: white;
}

/* Color/shape of burger icon bars on hover*/
.bm-burger-bars-hover {
    background: #1c4657;
}

/* Position and sizing of clickable cross button */
.bm-cross-button {
    display: none;
    height: 24px;
    width: 24px;
}

/* Color/shape of close button cross */
.bm-cross {
    background: #bdc3c7;
}

/*
Sidebar wrapper styles
Note: Beware of modifying this element as it can break the animations - you should not need to touch it in most cases
*/
.bm-menu-wrap {
    position: absolute;
    top:0;
    height: 100%;
}

/* General sidebar styles */
.bm-menu {
    background: #373a47;
    padding: 2.5em 1.5em 0;
    font-size: 1.15em;
}

/* Morph shape necessary with bubble or elastic */
.bm-morph-shape {
    fill: #373a47;
}

/* Wrapper for item list */
.bm-item-list {
    color: #b8b7ad;
    padding: 0.8em;
}

/* Individual item */
.bm-item {
    display: inline-block;
}

/* Styling of overlay */
.bm-overlay {
    position: absolute;
    top:0;
    height: 100%;
    background: rgba(0, 0, 0, 0.3);
}

html{scroll-behavior:smooth}

body{
    background-color: #4BC6B9;
}

.mainContainer{
    font-family: 'Lato';
    font-weight: lighter;
    color: white;
    text-shadow: 2px 2px black;
}

.titleContainer{
    margin: 10px 0 0 0;
    background-color: #1F5EA0;
    padding: 5px;

    text-align: center;
}

.textContainer{
    padding: 10px;
}

.sectionHeader{
    margin-bottom: 5px;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    border-bottom: 3px white solid;
}

.mainText{
    color: rgb(241, 241, 241);
    text-shadow: 1px 1px black;
    font-size: 25px;
}


@media screen and (min-width:768px){
    .mainContainer{
        margin: 0 100px;
    }
    .textContainer{
        padding: 20px;
        margin: 10px;
        border-radius: 20px;
    }
}
.background{
    background-color: rgba(160, 94, 94, 0.39);
}


.problem{
    background-color: #C4A256;
}

.mainProblemContainer{
    display: flex;
    align-items: center;
    flex-direction: column;
}

.dreamImg{
    padding:10px;
    width: 100%;
}

@media screen and (min-width:1024px){

    .mainProblemContainer{
        flex-direction: row;
    }
    
    .dreamImg{
        width:40%;
    }

}
.solution{
    background-color: #1F5EA0;
}

.solutions{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}

.solutionContainer{
    text-align: center;
    margin: 10px auto;
    width: 250px;
    padding:20px;
    background-color: rgba(0, 0, 0, 0.438);
    border-radius: 20px;
}
.aboutUs{
    background:rgba(160, 94, 94, 0.39);
}

.imageContainer{
    display: flex;
    justify-content: center;
}

.iSchoolLogo{
    width: 150px;
    height: 150px;
    border-radius: 50%;
    border: 2px black solid;
}
.team{
    background: #C4A256;
}

.teamContainer{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
}

.member{
    border-radius: 20px;
    text-align: center;
    position: relative;
    width: 250px;
    height: 250px;

    margin-bottom: 20px;
}

.memberImg{
    height:100%;
    width:100%;
    border-radius: 20px;
}

.memberInfo{
    visibility: hidden;
}

.memberImg:hover + .memberInfo, .memberInfo:hover, 
.memberImg:active + .memberInfo, .memberInfo:active{
    visibility: visible;
    -webkit-animation: fadeIn .5s;
            animation: fadeIn .5s;

    background-color: rgba(0, 0, 0, 0.404);
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;  
    border-bottom-left-radius: 20px;
    border-bottom-right-radius: 20px;
}

.socialMediaIcon{
    padding: 0 5px 3px 5px;
}

@-webkit-keyframes fadeIn {
    from { opacity: 0; }
      to { opacity: 1; }
}

@keyframes fadeIn {
    from { opacity: 0; }
      to { opacity: 1; }
}
.contactUs{
    background: #1F5EA0;
}

.emailLink{
    text-decoration: underline;
    color: white;
}


